import {
  baseUrl, //引入baseUrl 
} from "../config/env";
import axios from 'axios';
import store from '@/store/store.js';//在js文件引入store模块
axios.defaults.timeout = 10000; //设置请求时间
axios.defaults.baseURL = baseUrl;//设置默认接口地址
// 添加响应拦截器
axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  // return config;
  //这里经常搭配token使用，将token值配置到tokenkey中，将tokenkey放在请求头中
  if (config.url) {
    config.headers['Content-Type'] = "application/json"
    config.headers['accessToken'] = store.state.userInfo.token
  }
  return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});
// 添加响应拦截器
axios.interceptors.response.use(function (response) {
  // 对响应数据做点什么
  // console.log(response.data.status)
  if (response.data) {
    return response
  }
  else {
    store.state._this.$toast(response.data.error_msg);
  }
}, function (error) {
  // 对响应错误做点什么
  if (error && error.response) {
    console.log(error.response.status)
    switch (error.response.status) {
      case 400: store.state._this.$toast('请求错误(400)'); break;
      case 401: store.state._this.$toast('未授权，请重新登录(401)'); break;
      case 403: store.state._this.$toast('拒绝访问(403)'); break;
      case 404: store.state._this.$toast('请求出错(404)'); break;
      case 408: store.state._this.$toast('请求超时(408)'); break;
      case 500: store.state._this.$toast('服务器错误(500)'); break;
      case 501: store.state._this.$toast('服务未实现(501)'); break;
      case 502: store.state._this.$toast('网络错误(502)'); break;
      case 503: store.state._this.$toast('服务不可用(503)'); break;
      case 504: store.state._this.$toast('网络超时(504)'); break;
      case 505: store.state._this.$toast('HTTP版本不受支持(505)'); break;
      default: store.state._this.$toast('连接出错(${err.response.status})!');
    }
  } else {
    store.state._this.$toast('连接服务器失败!')
  }
  //store.state._this.$toast(error.message);
  return Promise.reject(error)
});
